// material-ui
import {
  Typography, Grid, Button, Avatar,
  Divider,
  Tooltip,
} from '@mui/material';

import { useEffect, useState } from 'react';
// project import
import {
  ArrowLeftOutlined,
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import {  post } from 'Urls/api';
import { useParams } from 'react-router-dom';
import { formatDate } from 'utils/formatDate';
import SubscribedUsers from './SubscribedUsers';


const UserDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [product_name, setproduct_name] = useState('')
  const [description, setDescription] = useState(0)
  const [price, setprice] = useState('')
  const [created_at, setCreated_at] = useState('')
  const [updated_at, setUpdated_at] = useState('')
  const getProductById = async () => {
    try {
      const postData = {
        user_id: id
      }
      const apiData = await post('user/getUserByUniqId', postData); // Specify the endpoint you want to call
      console.log("User")

      console.log(apiData)

      if (apiData.error) {
        console.log("ERROR")

        // toastAlert("error", apiData.message)


        // setNextloader(false)
      } else {
        setproduct_name(apiData?.result?.user_name)
        setprice(apiData?.result?.email)
        setDescription(apiData?.active_subscriptions.length)
        setCreated_at(apiData?.data?.product?.created_at)
        setUpdated_at(apiData?.data?.product?.updated_at)
      }
    } catch (error) {


    }
  }

  useEffect
    (() => {
      getProductById()
    }, [])

  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <Grid container spacing={2}>
            <Grid item xs={2} md={2} align="right">
              <Tooltip title="Back">


                <Button
                  onClick={() => {
                    navigate(`/users`)
                  }
                  }
                  className='poppinsRegularBold'
                  sx={{
                    // width: '100px',
                    paddingInline: '20px',
                    display: "block",
                    color: "#03356b",
                    backgroundColor: "none",
                    fontSize: '16px',
                    fontWeight: 600,
                    borderRadius: '50px',
                    textTransform: 'capitalize',
                    transition: 'background-color 0.3s, color 0.3s', // Add transition properties
                    transitionDelay: '0.3s', // Add transition delay for hover
                    // border: '1px solid #03356b',
                    '&:hover': {
                      backgroundColor: "#03356b",
                      color: 'white', // Change text color to blue on hover
                    },
                  }} >
                  <ArrowLeftOutlined />
                </Button>
              </Tooltip>

            </Grid>
            <Grid item xs={3} md={1} align="right">
              <Avatar style={{
                backgroundColor: "#03356b"
              }} >
                {product_name?.charAt(0)}
              </Avatar>

            </Grid>
            <Grid item xs={7} md={9} align="left">
              <Typography variant="h5" style={{ fontWeight: 200 }}>{product_name}</Typography>
              <Typography variant="h6" style={{ fontWeight: 200, color: 'gray' }}>{price}</Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} md={12}>
          <Divider />
        </Grid>
      </Grid>
      <Grid container spacing={2} style={{ marginBlock: '2%' }}>
        <Grid item xs={12} md={2}>
          <Typography variant="h5" style={{ fontWeight: 100, color: 'black' }}>Created at</Typography>
          <Typography variant="h6" style={{ fontWeight: 200, color: 'gray' }}>{formatDate(created_at)}</Typography>

        </Grid>
        <Grid item xs={12} md={2}>
          <Typography variant="h5" style={{ fontWeight: 100, color: 'black' }}>Updated at</Typography>
          <Typography variant="h6" style={{ fontWeight: 200, color: 'gray' }}>{formatDate(updated_at)}</Typography>

        </Grid>
        <Grid item xs={12} md={2}>
          <Typography variant="h5" style={{ fontWeight: 100, color: 'black' }}>Courses Enrolled</Typography>
          <Typography variant="h6" style={{ fontWeight: 200, color: 'gray' }}>{description}</Typography>

        </Grid>
      </Grid>

      <Grid container spacing={2}>
       
 
        <Grid item xs={12} md={12} style={{ backgroundColor: 'white', border: '1px solid lightGray', borderRadius: '20px' }}>
          <SubscribedUsers product_id={id} />
        </Grid>
      </Grid>

    </>
  )
};

export default UserDetail;
